@import url(https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&display=swap);
.header-container {
  background-color: #10162f;
  padding: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-container__logo {
  display: flex;
  align-items: center;
  color: white;
}

.header-container__logo h1 {
  margin-top: 40px;
}

.header-container__logo img {
  width: 120px;
  height: 100px;
}

.header-container__score-info {
  color: white;
  margin-right: 2rem;
  padding: 0;
}

.header-container__score-info li {
  list-style: none;
}

.la-star {
  color: yellow;
}

@media (max-width: 768px) {
  .header-container {
    flex-direction: column;
  }

  .header-container__logo {
    flex-direction: column;
  }

  .header-container__logo h1 {
    margin-top: 0;
    margin-bottom: 0;
  }

  .header-container__score-info {
    display: flex;
    margin-right: 0;
  }

  .header-container__score-info li {
    align-self: center;
    padding-right: 1rem;
  }
}

.card {
  padding: 1rem;
  text-align: center;
  background-color: #ffd50074;
  cursor: pointer;
  margin: 1em;
  transition: all 0.3s ease-in-out;
}

.card img {
  width: 120px;
  height: 120px;
}


.card:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
}

.card hr {
    border: none;
    height: 1px;
    background-color: #10162f;
}

@media(max-width: 768px) {
  .card img {
    width: 80px;
    height: 80px;
  }
}

.card-list-container {
  display: flex;
  width: 50%;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem auto;
  max-width: 1220px;
}

@media(max-width: 768px) {
  .card-list-container {
    width: 100%;
  }
}

.menu-btn-container:hover {
  background-color: #3510c5;
}

.menu-info-container {
  width: 350px;
  height: 250px;
  position: fixed;
  opacity: 1;
  bottom: 2rem;
  right: 2rem;
  box-shadow: 0 2px 5px rgba(16, 16, 16, 0.44);
  transition: all 0.3s ease-in-out;
  background-color: white;
  overflow-y: scroll;
}

.menu-info-container__header {
  color: white;
  text-align: center;
  background-color: #3a10e5;
  padding: 1rem;
}

.menu-info-container__header h3 {
  margin: 0;
}

.menu-info-container__body {
  padding: 1rem;
  border-bottom-left-radius: 10px;
  line-height: 1.5;
}

.menu-info-container__button {
  background-color: #3a10e5;
  color: #ffffff;
  padding: 1rem;
  border: none;
  width: 100%;
  cursor: pointer;
  transition: all 0.3s linear;
}

@media (max-width: 768px) {
  .menu-btn-container {
    bottom: 1rem;
    right: 1rem;
  }

  .menu-info-container {
      right: 1rem;
      bottom: 2rem;
      width: 250px;
      height: 200px;
  }

  .menu-info-container__body,
  .menu-info-container__header {
    padding: 0.7rem;
  }
}

* {
  box-sizing: border-box;
}

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Courier Prime', monospace, Arial 
}

